.Modal {
  height: 60vh;
  width: 30vw;

  background-color: #ffffff;
  overflow: auto;
}

.Overlay {
  position: fixed;

  top: 0;
  bottom: 0;
  right: 0;
  left: 0;

  display: flex;
  justify-content: center;
  align-items: center;

  background-color: rgba(0, 0, 0, 0.4);
}

.blockSize {
  width: 175px;
}
